<template>
  <div class="order">
      <header>我的订单</header>
      <div class="order-list">
          <div class="order-item" v-for="item in order" :key="item.id">
            <div class="item-list" v-for="n in item.list" :key="n.id">
                <img :src="n.pic" alt="">
                <div class="item-wrap">
                    <h3>{{n.title}}</h3>
                    <p>单价： {{n.price}}</p>
                    <div class="buy-number">
                        购买数量： {{n.buynum}}
                    </div>
                </div>
            </div>
            <div class="item-number-price">总数量：{{item.number}}&nbsp;&nbsp;&nbsp;&nbsp;总金额：{{item.price}}</div>
          </div>
      </div>
      <index-footer></index-footer>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { mapGetters } from 'vuex'
const indexFooter = () => import('@/components/footer/index-footer')
export default {
  components: {
      indexFooter
  },
  data: function () {
    return {
    }
  },
  computed: {
    ...mapState([
      'order'
    ]),
    ...mapGetters([
    ])
  },
  created(){
  },
  mounted() {
  },
  methods: {  
  }
}
</script>

<style scoped>
/**/
.order{
    background-color: #e9e8e8;
    padding-bottom: 100vh;
    position: relative;
    z-index: 0;
}
   header{
       width: 100vw;
       height: 12vw;
       background-color: #0089dc;
       text-align: center;
       line-height: 12vw;
       font-size: 4vw;
       color: #ffffff;
       position: fixed;
       top: 0;
       left: 0;
       z-index: 2;
   }
   .order-list{
       width: 100vw;
       margin-top: 12vw;
   }
   .order-item{
       position: relative;
       width: 100%;
       padding:2vw;
       margin-top: 1vw;
       background-color: #ffffff;
   }
    .order-item:first-child{
       margin-top: 0;
   }
   .item-list{
       position: relative;
       width: 100%;
   }
   .item-list img{
       height: 20vw;
   }
   .item-wrap{
       position: absolute;
       top: 0;
       right: 0;
       width: 70vw;
   }
    .item-wrap h3{
        height: 8vw;
        font-size: 4vw;
   }
    .item-wrap p{
        height: 6vw;
        font-size: 3vw;
   }
    .buy-number{
        height: 6vw;
        font-size: 3vw;
    }
    .item-number-price{
        width: 100%;
        height: 6vw;
        font-size: 4vw;
        line-height: 6vw;
        color:#ff624e;
    }
/**/
/*shoplist*/

/*shoplist*/
/*footer*/

/*footer*/
</style>



